
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';    // Imports various form things
import { Comment } from '../../shared/comment';


@IonicPage()
@Component({
  selector: 'page-addcomment',
  templateUrl: 'addcomment.html',
})
export class AddcommentPage {

  commentForm: FormGroup; // Sets up comment object as a type of FormGroup (from reactive library)
  comment: Comment;


  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public viewCtrl: ViewController,    // This ensures the page can be imported as a modal
    private formBuilder: FormBuilder    // Injects FormBuilder into constructor
    ) 

    // Below creates the reactive form within the constructor. 
    // The form is to be displayed completed upon opening the modal.
    {
      this.commentForm = this.formBuilder.group({
        Name: ['',Validators.compose([Validators.maxLength(30), Validators.minLength(3), Validators.pattern('[a-zA-Z ]*'), Validators.required])],
        rating: 1,
        // dateTime: [Date.now()],
        comment:['',Validators.compose([Validators.maxLength(30), Validators.minLength(3), Validators.required])]
      });
  }
  

  ionViewDidLoad() {
    console.log('ionViewDidLoad Page');
  }
  
  // The dismiss function - I imagine calling this will dismiss this page
  dismiss() {
    this.viewCtrl.dismiss();
  }
  
  // On submit, the values are logged to the console
  onSubmit() {
    this.comment = this.commentForm.value;
    this.comment.date = new Date().toISOString();
    console.log(this.commentForm.value);
    console.log(this.comment);
    this.viewCtrl.dismiss();
  }

}




