import { Component, OnInit, Inject } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Dish } from '../../shared/dish';
import { DishProvider } from '../../providers/dish/dish';
import { Promotion } from '../../shared/promotion';
import { PromotionProvider } from '../../providers/promotion/promotion';
import { Leader } from '../../shared/leader';
import { LeaderProvider } from '../../providers/leader/leader';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})

// OnInit makes stuff happen when this page is initialised
//

export class HomePage implements OnInit {

  // Defining local variable types within this component
  dish: Dish;
  promotion: Promotion;
  leader: Leader;
  dishErrMess: string;
  promoErrMess: string;
  leaderErrMess: string;

  constructor(
    public navCtrl: NavController,
  
    // Below 3 lines are the injection of providers
    private dishservice: DishProvider,
    private promotionservice: PromotionProvider,
    private leaderservice: LeaderProvider,
    // Injector of BaseUrl
    @Inject('BaseURL') private BaseURL) { }

  ngOnInit() {
    this.dishservice.getFeaturedDish() // calling dishService to getFeaturedDish, that returns an observable
       .subscribe(                                      // subscribe to observable, and implementation of 2 functions where
         dish => this.dish = dish,                      // server correctly returns the value; this is then set to this.dish
         errmess => this.dishErrMess = <any>errmess );  // server returns an error value; this is then set to this.dishErrMess

                                            // or 

    this.promotionservice.getFeaturedPromotion()
      .subscribe(
        promotion => this.promotion = promotion,
        errmess => this.promoErrMess = <any>errmess ); // Note that promoErrMess becomes a local variable used only in Home.html

        
    this.leaderservice.getFeaturedLeader()
      .subscribe(
        leader => this.leader = leader,
        errmess => this.leaderErrMess = <any>errmess );

  }

}


