import { Component, Inject } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, ActionSheetController, ModalController } from 'ionic-angular'; 
import { Dish } from '../../shared/dish';
import { FavoriteProvider } from '../../providers/favorite/favorite';
import { AddcommentPage } from '../addcomment/addcomment';

/**
 * Generated class for the DishdetailPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-dishdetail',
  templateUrl: 'dishdetail.html',
})
export class DishdetailPage {
  
  // variable of type: Variable

  dish: Dish;
  errMess: string;
  avgstars: string;
  numcomments: number;
  favorite: boolean = false;
  

  constructor(
    public navCtrl: 
    NavController, 
    public navParams: NavParams, // This is the part that takes in the click from the menu.ts file

    public modalCtrl: ModalController,

    @Inject('BaseURL') private BaseURL,
    private favoriteservice: FavoriteProvider,
    private toastCtrl: ToastController,
    
    public actionSheetController: ActionSheetController) {

    this.dish = navParams.get('dish');                // This is pushed in from menu.ts 
    this.numcomments = this.dish.comments.length;     // Counts the number of comments
    this.favorite = favoriteservice.isFavorite(this.dish.id); // This will check whether this dish is a favourite or not

    
          

    //Computing avgstars
    let total = 0;                                                    // Start with local variable of total
    this.dish.comments.forEach(comment => total += comment.rating );  // For each comment, total + the rating
    this.avgstars = (total/this.numcomments).toFixed(2);              // Total/(numberofcomments) = avgstars
  }                                         //toFixed rounds the number to 2 decimal figures

  ionViewDidLoad() {
    console.log('ionViewDidLoad DishdetailPage');
  }

  addToFavorites() {                                                  // This adds the dish to favourites
    console.log('Adding to Favorites', this.dish.id);
    this.favorite = this.favoriteservice.addFavorite(this.dish.id);   // Calls the addFavorite method from favoriteservice to add to favourites
 
    this.toastCtrl.create({                           // Creates toast with the below messages
      message: 'Dish ' + this.dish.id + ' added as favorite successfully',  
      position: 'middle',                             // Position of display on user screen
      duration: 3000}).present();                     // Presents for 3 seconds
  }

    // Opens reservation page modal on button click
    openComment() {
      let modal = this.modalCtrl.create(AddcommentPage); // Opens AddcommentPage as a modal on execution of method


      // Att 1
    //  modal.onDidDismiss(comment => {
    //   this.dish.comments.push(comment);
    //  });

      // Att 2
  //    modal.onDidDismiss((data) => {
  //      if (data) {
  //        this.dish.comments.push(data);
  //        }
  //      });

      modal.present();
    }
  


  async presentActionSheet() {
    const actionSheet = await this.actionSheetController.create({
      // header: 'Albums',
      buttons: [
        {
          text: 'Add to Favorites',
          icon: 'heart',
          
          handler: () => {
            console.log('Favorite clicked', this.addToFavorites());  // The inclusion of the function being 
                                                                     // called in the button handler makes the action of the button work  
          }
        },
        {
          text: 'Add Comment',
          icon: 'share',
          handler: () => {
            console.log('Share clicked', this.openComment());
          }
        }, 
        {
          text: 'Cancel',
          icon: 'close',
          role: 'cancel',                     // Note the role being a specific things for this button
          handler: () => {
            console.log('Cancel clicked');
          }
        }]
    });
    await actionSheet.present();
  }



}


