import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';    // Imports various form things

/**
 * Generated class for the ReservationPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-reservation',
  templateUrl: 'reservation.html',
})
export class ReservationPage {

  reservation: FormGroup; // Sets up reservation object as a type of FormGroup (from reactive library)


  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public viewCtrl: ViewController,    // This ensures the page can be imported as a modal
    private formBuilder: FormBuilder    // Injects FormBuilder into constructor
    ) 

    // Below creates the reactive form within the constructor. 
    // The form is to be displayed completed upon opening the modal.
    {
      this.reservation = this.formBuilder.group({
        guests: 3,
        smoking: false,
        dateTime: ['', Validators.required],
      });
  }
  

  ionViewDidLoad() {
    console.log('ionViewDidLoad ReservationPage');
  }
  
  // The dismiss function - I imagine calling this will dismiss this page
  dismiss() {
    this.viewCtrl.dismiss();
  }
  
  // On submit, the values are logged to the console
  onSubmit() {
    console.log(this.reservation.value);
    this.viewCtrl.dismiss();
  }

}



