import { Component, OnInit, Inject } from '@angular/core';
import { IonicPage, NavController, NavParams, ItemSliding, ToastController, LoadingController, AlertController } from 'ionic-angular';
import { Dish } from '../../shared/dish';
import { FavoriteProvider } from '../../providers/favorite/favorite';       


/**
 * Generated class for the FavoritesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-favorites',
  templateUrl: 'favorites.html',
})
export class FavoritesPage implements OnInit{

  favorites: Dish[];      // Contains list of all favourite dishes
  errMess: string;        // Error strings

  constructor(                        // Makes objects available within the object
    public navCtrl: NavController, 
    public navParams: NavParams,
    private favoriteservice: FavoriteProvider,  // Favourite provider - this injects favourites
    @Inject('BaseURL') 
    private BaseURL,                            // Injects BaseURL
    public toastCtrl: ToastController,          // Provides visual confirmation that an operation has occured
    private loadingCtrl: LoadingController,     // Keeps user informed that work is happening in the background
    private alertCtrl: AlertController) {       // Seek user's confirmation before proceeding with an operation
  }
  
  
  ngOnInit() {
    this.favoriteservice.getFavorites()                   // Gets list of favourites on initialisation
      .subscribe(                                         // Subscribes to list of results, 
        favorites => this.favorites = favorites,          // If positive result assignes to favourites
        errmess => this.errMess = errmess);               // If negative assignes error message
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad FavoritesPage');
  }

  // This is the delete function - function defined locally on the page.
  deleteFavorite(item: ItemSliding, id: number) {
    console.log('delete', id);
                                                    // This section is the alert advising a user that they are about to detele
    let alert = this.alertCtrl.create({                  
      title: 'Confirm Delete',
      message: 'Do you want to delete Dish '+ id,
      buttons: [
        {                                           // Javascript option 1
          text: 'Cancel',                           // Option to cancel the deletion of favourite
          role: 'cancel',
          handler: () => {                          // This is a function undertaken on button press
            console.log('Delete cancelled');
          }
        },
        {                                           // Javascript option 2
          text: 'Delete',                                       // If the user selects the delete button then the;
          handler: () => {                                      // This are the functions undertaken on button press.
            let loading = this.loadingCtrl.create({             // This is the loading popover code
              content: 'Deleting . . .'                        
            });
            let toast = this.toastCtrl.create({                   // Creates toast content
              message: 'Dish ' + id + ' deleted successfully',    // Confirmation of subject variable attributes
              duration: 3000});                                   // 3 Second duration
            loading.present();                                    // Calling the present method, the loading toast will be presented
            
            this.favoriteservice.deleteFavorite(id)               // Deletes favourite and dismisse
              .subscribe(
                favorites => {this.favorites = favorites; loading.dismiss(); toast.present(); } ,
                
                errmess =>{ this.errMess = errmess; loading.dismiss(); });
          }
        }
      ]
    });
  
    alert.present();

    item.close();

  }
}


