import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, ModalController } from 'ionic-angular';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { Storage } from '@ionic/storage';
import { User } from '../../shared/user';

import { RegisterPage } from '../register/register';

/**
 * Generated class for the LoginPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  loginForm: FormGroup;                         // Generates the login form
  user: User = {username: '', password: ''};    // user initialised as type User with '' as initialised values 

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public viewCtrl: ViewController,
    private formBuilder: FormBuilder,
    private storage: Storage,
    private modalCtrl: ModalController ) {


      // This section retrives login information that has been strored locally by the user
      // Local storage of login credentials to be used when connecting to server

      storage.get('user').then(user => {            // Supports promise interface - and undertakes action depending on response
        if (user) {                                 // If user returned from storage  (if user not null)
          console.log(user);                        // Print user credentials in console
          this.user = user;                         
          this.loginForm.patchValue({               // patchValue function inserts values from user
              'username': this.user.username,       // Inserts user.username as 'username'
              'password': this.user.password        // Inserts user.password as 'password'
            });
        }
        else                                        
          console.log('user not defined');           // User has not saved their credentials locally
      });


      this.loginForm = this.formBuilder.group({      // sets up loginForm as the following formBuilder group ({ xx })
        username: ['', Validators.required],         // Related to inputs by ion-input formControlName='username' attribute
        password: ['',Validators.required],         
        remember: true
      });

  }

  openRegister() {
    let modal = this.modalCtrl.create(RegisterPage);
    modal.present();
    modal.onDidDismiss(() => this.dismiss())
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }

  dismiss() {                                     // This method will dismiss the modal
    this.viewCtrl.dismiss();
  }

  // When the form is submitted this is done

  onSubmit() {
    console.log(this.loginForm.value, this.user);
    this.user.username = this.loginForm.get('username').value;    // Sets the user.username as the loginForm.xx.value
    this.user.password = this.loginForm.get('password').value;    // Sets the user.username as the loginForm.xx.value
    console.log(this.user);

    // Check box actions to follow
    if(this.loginForm.get('remember').value)                      // If remember is true 
      this.storage.set('user', this.user)                         // Set (save) the 'user' credentials as this.user

    else                                                          // If not true
      this.storage.remove('user');                                // Remove 'user' values

    this.viewCtrl.dismiss();                                      // Closes modal
  }
}



