import { Component, OnInit, Inject } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { LeaderProvider } from '../../providers/leader/leader';
import { Leader } from '../../shared/leader';

/**
 * Generated class for the AboutPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-about',
  templateUrl: 'about.html',
})
export class AboutPage implements OnInit{
  leaders: Leader[];
  errMess: FunctionStringCallback;


  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    
    private leaderservice: LeaderProvider,
    
    // The base url is injected into the code - this was configured from the app module
    @Inject('BaseURL') private BaseURL

  ) { }

    // ngOnInit will go and fetch the data on init by calling the getDishes(method) of the dishservice
    ngOnInit() {
      this.leaderservice.getLeaders()
        // We will subscribe to the returned value from the above method
        .subscribe(
          leaders => this.leaders = leaders, // When leaders is returned, then this.leaders=leaders. 
                                          //(So when the getLeaders function returns leaders), leaders is assigned to this.leaders
          
          errmess => this.errMess = <any>errmess); //When errmess is returned then this.errMess = the errmess 
    }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AboutPage');
  }

}



